.modal {
  display: block;
  opacity: 0;
  -webkit-transition: 0.2s ease-in;
  transition: 0.2s ease-in;
}
.modal.is-active {
    display: block;
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
    opacity: 1;
}
.modal.is-active .modal-background {
      display: block;
}
.modal.is-active .modal-content {
      display: block;
}
.modal.is-active button.close {
      display: block;
}
.modal .modal-background {
    display: none;
    background-color: #333;
    opacity: 0.6;
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
}
.modal .modal-content {
    display: none;
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
    height: 80vh;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 10vh;
}
.modal button.close {
    display: none;
    position: fixed;
    top: 1em;
    right: 1em;
    cursor: pointer;
    height: 60px;
    width: 60px;
    background-color: transparent;
    border-color: transparent;
    color: #222;
    opacity: 1;
}
.modal button.close:hover {
      color: #EEE;
}

